.win-popup {
  display: flex;
  flex-direction: column;
  justify-content: center;

  & .modal__content {
    min-width: 35rem;
    min-height: 20rem;
    box-shadow: 0 0 10rem 1rem var(--color-shadow-primary);
    background-color: var(--color-background-win-popup);
    color: $color-font-light-gray;
  }

  &_title {
    width: 100%;
    padding: 1rem 0 1.4rem;
    border-bottom: 0.2rem solid;
    font-size: 2.6rem;
    text-align: center;
    color: var(--color-font-win);
    text-transform: uppercase;
    opacity: 70%;
  }

  &_info {
    &__current-result,
    &__best-result {
      display: grid;
      place-items: center;
      font-size: 2rem;
      margin: 3rem 0 0;
      color: var(--color-font-info);
    }

    &__current-result {
      margin: 3rem 2rem 0;
      padding: 2rem;
      border: 0.1rem solid var(--color-font-win);
      box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    }

    &__best-result {
      width: 100%;
      display: grid;
      place-items: center;
      margin-top: 2rem;
      padding-bottom: 2rem;
      border-radius: 0 0 1rem 1rem;
      text-align: center;
      font-size: 1.5rem;

      & h2 {
        margin: 1rem 0;
        font-size: 1.7rem;
        opacity: 40%;
      }
    }
  }
}
